#main-nav-div{
    display: flex;
    justify-content: space-between;
    padding: 1rem 2rem;
    align-items: center;
    background-color: #fdbec6;
}
.nav-items{
    display: flex;
    justify-content: space-between;
    padding: 0 3rem;
    width: 40%;
}
a{
    text-decoration: none;
}
.nav-text{
    color: #333333;
    font-family: 'Playfair Display', serif;
    font-weight: 600;
}